  corraAboutProject1:
    ' Corra — это готовая к использованию платформа, позволяющая выполнять монетизацию цифрового содержимого при помощи технологии NFT. Corra позволит каждому бесплатно создавать и регистрировать на бирже NFT. Это означает, что независимо от вашего возраста, биографии или уровня доходов у вас будет равная возможность монетизировать свое цифровое содержимое из Facebook, Instagram, Twitter, YouTube и других социальных сетей. Это включает публикации в Facebook, записи в социальной сети микроблогов Twitter, фотографии Instagram, мемы, песни, видео Tik Tok или практически любую форму цифрового содержимого, опубликованного в Интернете. Независимо от того, являетесь ли вы влиятельным лицом, знаменитостью, общественным деятелем, автором содержимого в YouTube, моделью Instagram, студентом или просто человеком, у вас есть равные шансы превратить свое цифровое содержимое в реальные деньги. Эта платформа позволяет предоставлять совместный доступ друзьям, поклонникам, подписчикам и сообществу, чтобы им могла принадлежать часть вашей интеллектуальной собственности с целью рекламы вашей марки и содержимого. Помимо этого, в Corra будет токен под названием CORA, который будет использоваться для голосования за ваши зарегистрированные на бирже NFT. Чем больше голосов вы получите за свой зарегистрированный продукт, тем более высокий рейтинг и заметность на странице аукционов Corra будет иметь ваш продукт. Это очень схоже с платным маркетингом в результатах поиска Google, при этом целью является повышение заметности вашего продукта и повышение вероятности получить заявку. Вы можете повысить свои голоса, поделившись ими с друзьями и подписчиками и привлечь их к голосованию за вас. С использованием токенов CORA каждый также может проголосовать за ваш продукт. Вы можете получить токены CORA на децентрализованных и централизованных биржах криптовалюты с использованием официальных валют или криптовалют.',
  cowAboutProject1:
    '«CoinWind — это финансовая платформа интеллектуального майнинга DeFi, которая автоматически сопоставляет заложенные токены с парами посредством контрактов, применяет для максимального повышения дохода пользователей стратегию минимального хеджирования без потерь. Таким образом, CoinWind эффективно решает следующие проблемы пользователей:низкий доход от майнинга одного токена, большая потеря майнинга LP и т. д.»',
  cowAboutProject2:
    ' CoinWind ставит перед собой цель создать банк цифровых активов DeFi, выведя на рынок продукт, сочетающий в себе высокую доходность, безопасность и надежность. CoinWind выполнит управление и передаст различные токены, депонированные пользователями в различные пулы, затем управлять средствами того или иного пула токенов, чтобы участвовать в майнинге ликвидности в соответствии с различными стратегиями. Ну и наконец — повышать выгоду для всех пользователей. CoinWind может эффективно снизить временную потерю при майнинге ликвидности посредством стратегии комбинирования.',
